<template>
    <div class="historic"> 
        <div class="historicBoard">
            <div class="historicBoardTitle">Title</div>
            <div class="historicBoardSubtitle">Subtitle</div>
            <div class="historicBoardLine"></div>
            <div class="historicLogo">CommentCalc - HenCan</div> 
        </div>
        <div class="historicBtn">
            <div class="historicBtnPreview" @click="PreviewHistory">Preview</div>
            <div class="historicBtnClear" @click="ClearHistory">Clear</div>
        </div>
    </div>
</template>
<script>
import html2canvas from 'html2canvas'

export default {
    name: 'Historic',
    methods: {
        PreviewHistory: function() {
            var imageData
            html2canvas(document.querySelector(".historicBoard"), {
                scale: 2,
            }).then(canvas => {
            document.getElementById('historicPreviewCanvas').appendChild(canvas);
            imageData = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream");
            // Now browser starts downloading it instead of just showing it
            document.querySelector("#historicPreviewBtnDownload").setAttribute("download", "commentCalc_app.png")
            document.querySelector("#historicPreviewBtnDownload").setAttribute("href", imageData)
            document.querySelector(".historicPreview").style.display = "block"
            window.scrollTo(0, 1500)
        })},
        ClearHistory: function() {
            document.querySelector('.historicBoardTitle').innerHTML = "Title"
            document.querySelector('.historicBoardSubtitle').innerHTML = "Subtitle"
            document.querySelector('.historicBoardLine').innerHTML = ""
        }

    }
}
</script>
<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap');
.historic {
    width: 50%;
    max-width: 350px;
    height: calc(100vh - 40px);
    max-height: 600px;
    background-color: white;
    border: solid 1px gray;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    font-family: 'Roboto Mono', monospace;
}
.historicBoard {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: auto;
    margin: 5px 2% 0;
    height: 90%;
    overflow-y: scroll;
}
.historicBoardTitle {
    padding: 5px 0 2.5px;
    margin: 0 5px;
    text-align: center;
    font-size: 1.5em;
    word-wrap: break-word;
    cursor: pointer;
}
.historicBoardSubtitle {
    text-align: center;
    font-size: 1em;
    word-wrap: break-word;
    padding: 2.5px 0 10px;
    margin: 0 5px;
    border-bottom: solid 1.5px;
    cursor: pointer;
}
.historicBoardLine {
    text-align: right;
    padding: 0 10px; 
    word-wrap: break-word;
    padding: 5px;
    cursor: pointer;  
}
.historicBoardLine span {
    text-align: left;
    word-wrap: break-word;
    cursor: pointer;
}
.historicLogo {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: auto;
    height: 5%;
    margin: auto 2% 5px;
    padding: 5px;
    text-align: center;
    font-size: 0.75em;
}
.historicBtn {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    width: auto;
    font-size: 0.75em;
    margin-top: 5px;
}
.historicBtnPreview {
    margin: 0 auto;
    border: solid 1px;
    padding: 2.5px 10px;
    border-radius: 10px;
    background-color: rgb(81, 81, 250);
    color: white;
    font-weight: bold;
    height: 30px;
    line-height: 30px;
    cursor: pointer;
}
.historicBtnPreview:hover {
    background-color: rgb(31, 31, 255);
}
.historicBtnClear {
    margin: 0 auto;
    border: solid 1px;
    padding: 2.5px 10px;
    border-radius: 10px;
    background-color: firebrick;
    color: white;
    font-weight: bold;
    height: 30px;
    line-height: 30px;
    cursor: pointer;
}
.historicBtnClear:hover {
    background-color: red;
}
/* Configuração Mobile */
/* Mobile */
@media(max-width: 620px) {
    .historic {
        width: 100%;
        margin: 10px auto 0;
    }
}
</style>