<template>
    <div class="calculator">
        <div class="calculatorDisplay">
            <div class="calculatorDisplayMsg">{{outputMsg}}</div>
            <div class="calculatorDisplaySub">{{inputSub}}</div>
            <div class="calculatorDisplayMain">{{inputNum}}</div>
        </div>
        <div class="calculatorKeyboard">
            <Button class="calculatorKeyboardBtn btnOpr2" label="AC" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnOpr2" label="⌫" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnOpr2" label="%" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnOpr1" label="÷" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="7" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="8" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="9" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnOpr1" label="×" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="4" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="5" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="6" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnOpr1" label="-" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="1" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="2" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="3" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnOpr1" label="+" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="⁺/₋" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="0" @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnNum" label="," @onClick="keyFunction"/>
            <Button class="calculatorKeyboardBtn btnOpr1" label="=" @onClick="keyFunction"/>
        </div>
    </div>
</template>
<script>
import Button from "./Button"

export default {
    name: 'Calculator',
    components: { Button },
    data: function() {
        return {
            hist: "",
            commaOp: 0,
            posOp: 0,
            isEqualActived: 0,
            isAnyActived: 0,
            inputMem: "",
            inputNum: "0",
            outputMsg: "",
            inputSub: 0
        }
    },
    // created: 
    // },
    methods: {
        keyFunction: function(btnVal) {
            switch (btnVal) {
                case 'AC':
                    this.inputNum = "0"
                    this.inputSub = 0
                    this.commaOp = 0
                    this.posOp = 0
                    this.isEqualActived = 0
                    this.isAnyActived = 0
                    this.inputMem = ""
                    this.outputMsg = ""
                    break;
                case "=":
                    if (this.isEqualActived === 0 && this.isAnyActived == 1) {
                        this.inputSub += this.inputNum
                        this.inputMem += this.inputNum
                        this.inputMem = this.inputMem.replace(",", ".")
                        this.inputMem = eval(this.inputMem)
                        this.inputNum = this.inputMem.toString().replace(".", ",")
                        if (this.inputNum.length >15) {
                            this.inputNum = this.inputMem.toFixed(3).replace(".", ",")
                            this.outputMsg = "[Aprox.]"
                            if (this.inputNum.length >15) {
                                this.outputMsg = "Error - MaxDigitsExceeded"
                                this.inputSub = "-"
                                this.inputNum = "-"
                                break;
                            }
                        }
                        document.querySelector('.historicBoardLine').innerHTML += "<br><span>✎</span><br>"
                        document.querySelector('.historicBoardLine').innerHTML += this.inputSub + " = " + this.inputNum + "<br>"
                        document.querySelector('.historicBoardLine').innerHTML = document.querySelector('.historicBoardLine').innerHTML.replace(".", ",")
                        this.posOp = 1
                        this.isEqualActived = 1
                        this.isAnyActived = 0

                        this.hist = document.querySelectorAll('.historicBoardTitle, .historicBoardSubtitle, .historicBoardLine span')

                        for (var i = 0; i < this.hist.length; i++) {
                            this.hist[i].onclick = function () {
                            this.innerHTML = "✎ " + prompt("Insira seu texto personalido")
                            }
                        }
                        }break;
                case "⌫":
                    if (this.inputNum == "") {
                        this.inputNum = 0
                    } else if (this.inputNum.length < 2) {
                        this.inputNum = 0
                    } else {
                        this.inputNum = this.inputNum.substring(0, (this.inputNum.length - 1))
                        this.posOp = 0
                        this.outputMsg = ""
                    } break;
                case "⁺/₋":
                    if (this.inputNum == "") {
                        this.inputNum = 0
                    } else if (this.inputNum === 0) {
                        this.inputNum = 0
                    } else {
                        this.inputNum = eval(this.inputNum) * (-1)
                    }break;
                case "%":
                    if (this.inputNum == "") {
                        this.inputNum = 0
                    } else if (this.inputNum === 0) {
                        this.inputNum = 0
                    } else {
                        this.inputNum = eval(this.inputNum) / (100)
                        this.inputNum = this.inputNum.toString().replace(".", ",")
                        this.outputMsg = ""
                    }break;
                case "÷":
                    if (this.inputNum == "") {
                        this.inputNum = 0
                    } else if (this.inputNum == 0) {
                        this.inputNum = 0
                    } else if (this.isAnyActived == 0) {
                        this.inputSub = this.inputNum + btnVal
                        this.inputMem = this.inputNum + btnVal
                        this.inputMem = this.inputMem.replace(btnVal, "/")
                        this.inputMem = this.inputMem.replace(",", ".")
                        this.posOp = 1
                        this.commaOp = 0
                        this.isEqualActived = 0
                        this.isAnyActived = 1
                        this.outputMsg = ""
                    } else {
                        this.inputSub += this.inputNum + btnVal
                        this.inputMem += this.inputNum + btnVal
                        this.inputMem = this.inputMem.replace(btnVal, "/")
                        this.inputMem = this.inputMem.replace(btnVal, ".")
                        this.posOp = 1
                        this.commaOp = 0
                        this.isEqualActived = 0
                        this.isAnyActived = 1
                        this.outputMsg = ""
                    } break;
                case "×":
                    if (this.inputNum == "") {
                        this.inputNum = 0
                    } else if (this.inputNum == 0) {
                        this.inputNum = 0
                    } else if (this.isAnyActived == 0) {
                        this.inputSub = this.inputNum + btnVal
                        this.inputMem = this.inputNum + btnVal
                        this.inputMem = this.inputMem.replace(btnVal, "*")
                        this.inputMem = this.inputMem.replace(",", ".")
                        this.posOp = 1
                        this.commaOp = 0
                        this.isEqualActived = 0
                        this.isAnyActived = 1
                        this.outputMsg = ""
                    } else {
                        this.inputSub += this.inputNum + btnVal
                        this.inputMem += this.inputNum + btnVal
                        this.inputMem = this.inputMem.replace(btnVal, "*")
                        this.inputMem = this.inputMem.replace(btnVal, ".")
                        this.posOp = 1
                        this.commaOp = 0
                        this.isEqualActived = 0
                        this.isAnyActived = 1
                        this.outputMsg = ""
                    } break;
                case ",":
                    if (this.commaOp == 0 && this.inputNum.includes(',') == false) {
                        this.inputNum += btnVal
                        this.commaOp = 1
                    } break;
                case "+":
                    if (this.isAnyActived == 0) {
                        this.inputSub = this.inputNum + btnVal
                        this.inputMem = this.inputNum + btnVal
                        this.inputMem = this.inputMem.replace(",", ".")
                        this.posOp = 1
                        this.commaOp = 0
                        this.isEqualActived = 0
                        this.isAnyActived = 1
                        this.outputMsg = ""
                    } else {
                        this.inputSub += this.inputNum + btnVal
                        this.inputMem += this.inputNum + btnVal
                        this.inputMem = this.inputMem.replace(",", ".")
                        this.posOp = 1
                        this.commaOp = 0
                        this.isEqualActived = 0
                        this.isAnyActived = 1
                        this.outputMsg = ""
                    } break;
                case "-":
                    if (this.isAnyActived == 0) {
                        this.inputSub = this.inputNum + btnVal
                        this.inputMem = this.inputNum + btnVal
                        this.inputMem = this.inputMem.replace(",", ".")
                        this.posOp = 1
                        this.commaOp = 0
                        this.isEqualActived = 0
                        this.isAnyActived = 1
                        this.outputMsg = ""
                    } else {
                        this.inputSub += this.inputNum + btnVal
                        this.inputMem += this.inputNum + btnVal
                        this.inputMem = this.inputMem.replace(",", ".")
                        this.posOp = 1
                        this.commaOp = 0
                        this.isEqualActived = 0
                        this.isAnyActived = 1
                        this.outputMsg = ""
                    } break;
                default:
                    if (this.posOp != 0 && this.inputNum != "-") {
                        this.inputNum = btnVal
                        this.posOp = 0
                        } 
                    else if (this.inputNum.length < 14) {
                        if (this.inputNum == 0) {
                            this.inputNum = btnVal
                        } else {
                            this.inputNum += btnVal
                        }
                        } else {
                            this.outputMsg = "Max Digits"
                    } break;
        }
        }
    }
}
</script>
<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap');
.calculator {
    width: 50%;
    max-width: 350px;
    height: calc(100vh - 40px);
    max-height: 600px;
    background-color: black;
    border: solid 1px gray;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    color: white;
}
.calculatorDisplay {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: auto;
    margin: 5px 2% 0;
    padding: 5px;
    min-height: 30%;
    max-height: calc(40vh - 20px);
}
.calculatorDisplayMsg {
    padding: 2px;
    text-align: right;
    font-size: 1em;
    margin: auto 0;
    font-family: 'Roboto Mono', monospace;
}
.calculatorDisplaySub {
    padding: 2px;
    text-align: right;
    font-size: 1.25em;
    margin: auto 0;
    word-wrap: break-word;
    font-family: 'Roboto Mono', monospace;
}
.calculatorDisplayMain {
    padding: 2px;
    text-align: right;
    font-size: 2em;
    margin: auto 0;
    font-family: 'Roboto Mono', monospace;
}
.calculatorKeyboard {
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: calc((60vh - 20px) / 5);
    margin: 5px 2% 5px;

}
.calculatorKeyboardBtn {
    box-sizing: border-box;
    border-right: solid 1px black;
    border-bottom: solid 1px black;
    border-radius: 40px;
    margin-top: 2px;
    margin-bottom: 2px;
    width: auto;
    height: calc((60vh - 30px) / 5);
    text-align: center;
    font-size: 1.25em;
    font-weight: bold;
    box-shadow: 0px 0px 5px rgba(153, 153, 153, 0.5) inset;
    cursor: pointer;
    outline: none;
}
.btnNum {
    background-color: rgb(50,51,51);
    color: rgb(246, 246, 246);
}
.btnNum:active {
    background-color: rgba(50, 51, 51, 0.5);
}
.btnOpr1 {
    background-color: rgb(255, 158, 10);
    color: rgb(255, 246, 233);
}
.btnOpr2 {
    background-color: rgb(165, 165, 165);
    color: rgb(11, 11, 11);
}
/* Configuração Mobile */
/* Mobile */
@media(max-width: 620px) {
    .calculator {
        width: 100%;
        height: calc(100vh -40px);
        margin: 0 auto;
    }
}
</style>